.couponcodecontainer {
  position: relative;
  width: 100px;
  height: 80px;
  border-radius: 10px;
  background: linear-gradient(
    to top left,
    #eaf5fd 50%,
    #afc2f7 50%
  );
}

.coupondiscountcontainer {
  position: absolute;
  width: 0;
  height: 0;
  clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
  z-index: 3;
}

.coupondiscounttext {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  color:black;
  font-weight: bold;
}
