.topnav {
  background-color: #333;
  overflow: hidden;
  width: 71.5%;
border: 1px solid #333;
border-top: none;
border-bottom: none;
 
}

.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #04AA6D;
  color: white;
  background-color: #ddd;
  color:black;
}
