.loader-container {
   display: flex;
   align-items: center;
   justify-content: center;
   height: 5vh;
 }
 
 @media  screen and (max-width:540px) {
  .loader-container{
    margin-top: 50px;
  }
 }
 .loader {
   border: 8px solid #f3f3f3;
   border-top: 8px solid #3498db;
   border-radius: 50%;
   width: 50px;
   height: 50px;
   animation: spin 1s linear infinite;
 }
 
 @keyframes spin {
   0% { transform: rotate(0deg); }
   100% { transform: rotate(360deg); }
 }