
$margin-Top:"10px";
$margin-Bottom:"20px";
@mixin marginBottom($marginBottom){
  margin-Bottom:$marginBottom;
}
@mixin marginTop($marginTop){
  margin-top:$marginTop;
}
@mixin alignment{
 margin-top: 32px;
 text-align: right;
}

.textcolor{
  color: #4a463d;
  font-size: 24px;
  font-weight: bold;
}
.resendotptext{
  font-size: 18px;
}

@media screen and (max-width:430px) {
  .textcolor{
   font-size: 16px
  }
  
  .resendotptext{
    font-size: 12px;
  }
}
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
  .lable {
    position: absolute;
    top: 50%;
    left: 40px;
    transform: translateY(-100%);
    pointer-events: none;
    transition: top 0.3s ease, font-size 0.3s ease;
  }
  .mobile-label.focused {
    top: -15px; 
    font-size: 16px;
    left: 18px;
    padding-bottom: 30px;
  }
  .centered {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: top 0.3s ease, transform 0.3s ease;
  }
  .otpcontainer{
    display: flex;
    margin-top: 10px;
  }
  .otpbtncontianer-responsive{
    display: none;
  }
  .creditredemption-container{
    display: flex;
  }
  .creditpointsbtn{
    width: 10%;
  }
  .otpbtn{
width: 29%;
display: none;
  }
  .verifyotpbtn{
    width: 100%;
  }
  
  @media  screen and (max-width:540px) {
    .otpbtncontianer-responsive{
      display: flex;
    }
    .points-container{
      flex-direction: column;
      
    }
    .otpcontainer{
      display: none
    }
    .otpbtn{
      width: 100%;
      display: flex;
    }
    .verifyotpbtn{
      width: 100%;
    }
    .creditpointsbtn{
      width: 100%;
    }
  }

  .button-element-able{
    background-color: #111111;
    border-radius: 0.33rem;
    border-color: #111111;
    border-width: 0;
    color: #f9f9f9;
    font-family: inherit;
    font-size: 0.9rem;
    font-style: normal;
    font-weight: 500;
    line-height: inherit;
    padding-top: 0.6rem;
    padding-right: 1rem;
    padding-bottom: 0.6rem;
    padding-left: 1rem;
    align-items: center;
    display: inline-flex;
    height: auto;
    justify-content: center;
    position: relative;
    text-align: center;
    transition: box-shadow .1s linear;
    cursor: pointer;
    text-decoration: none;
  }
  .button-element-disabled{
    background-color: #111111;
    border-radius: 0.33rem;
    border-color: #111111;
    border-width: 0;
    color: #f9f9f9;
    font-family: inherit;
    font-size: 0.9rem;
    font-style: normal;
    font-weight: 500;
    line-height: inherit;
    padding-top: 0.6rem;
    padding-right: 1rem;
    padding-bottom: 0.6rem;
    padding-left: 1rem;
    align-items: center;
    display: inline-flex;
    height: auto;
    justify-content: center;
    position: relative;
    text-align: center;
    transition: box-shadow .1s linear;
   cursor: not-allowed;
    text-decoration: none;
  }
 .error-msg-outlineing{
  padding-left: 120px;
  margin-top: 20px;
 }

 @media  screen and (max-width:540px) {
  .error-msg-outlineing{
    padding-left: 50px;
  }
 }
 @media  screen and (max-width:430px) {
  .error-msg-outlineing{
    padding-left: 50px;
  }
 }
 @media  screen and (max-width:280px) {
  .error-msg-outlineing{
    padding-left: 10px;
  }
  
 }
